// General
// ========================================
a {
  text-decoration: none;
  color: $main-red;
  display: inline-block;

  &:hover,
  &:focus,
  &:active {
    color: lighten($main-red, 20);
  }
}

a.disabled {
  text-decoration: none;
  pointer-events: none;
  color: $lgrey;
}

button {
  outline: none;
  border: none;
  appearance: none;
  border-radius: 0;
  background: $white;

  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
  }
}

// Custom Class Button
// ==========================================
.btn {
  background-color: $white;
  color: $main-red;
  font-size: $ssub;
  width: 100%;
  height: 41px;
  padding: 20px 0;
  border: 1.5px solid $main-red;
  text-align: center;

  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
  }

  &:disabled {
    background-color: lighten($light-red, 30);
  }
}

.btnGap {
  // background: url('../images/welcome-min.jpg');
  background-size: cover;
}

.btnMed {
  font-size: $sub;
  height: 45px;
  line-height: 45px;
}

.btnWht {
  background-color: $white;
  color: $main-red;
}

.btnLink {
  background: transparent;
  color: $dark;
  text-decoration: underline;
  cursor: pointer;

  &:disabled {
    opacity: 0.6;
  }
}

.btnSquare {
  font-weight: 700;
  background-color: $white;
  border: 2px solid $main-red;
  padding: 0 24px;
  color: $main-red;
  letter-spacing: 0.5px;
}
.btnSquareFill {
  @extend .btnSquare;
  color: $white;
  background-color: $main-red;
}
.btnTxtImg,
.btnImgTxt {
  & > * {
    display: inline-block;
    vertical-align: middle;
  }
  span {
    line-height: 1;
  }
}
.btnTxtImg {
  img {
    margin-left: 8px;
  }
}
.btnImgTxt {
  img {
    margin-right: 8px;
  }
}

// Custom Class Link
// ========================================
.linkWht {
  color: $white;

  &:hover {
    color: darken($main-red, 15);
  }
}

.linkMgrey {
  color: $main-grey;
  &:hover {
    color: lighten($main-grey, 10);
  }
}

.linkGrey {
  color: $grey;

  &:hover {
    color: lighten($grey, 10);
  }
}

.linkBlack {
  color: $dark;

  &:hover {
    color: lighten($dark, 10);
  }
}

.linkU {
  color: #333;
  text-decoration: underline;
  font: 400 18px "NeulandGrotesk-Condensed";

  &.login {
    background: #fff;
    padding: 4px 16px;
    outline: none;
    text-decoration: none;
  }
}

.linkNou {
  &:hover,
  &:focus,
  &:active {
    text-decoration: none;
  }
}

.link__info {
  display: block;
  width: 25px;
  height: 25px;
  background: $main-red;
  color: $white;
  line-height: 25px;
  text-align: center;
  border-radius: 50%;

  &:hover {
    color: $white;
    background: lighten($main-red, 20);
  }
}

.plink {
  position: relative;

  & + & {
    &:before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      width: 1px;
      height: 15px;
      background: $grey;
      margin: 0 8px;
    }
  }
}

// Specified link
// =======================================
.linkTerms {
  color: $form-color-text;

  &:hover,
  &:focus {
    text-decoration: none;
  }

  .fa {
    display: inline-block;
    font-size: $sml;
    vertical-align: center;
  }
}

// Navigation Icon
// =======================================
.nav__back {
  font-size: 24px;
  line-height: 50px;
  padding: 8px 0;

  a {
    width: 50px;
    height: 50px;
    text-align: center;
  }
}

.toHome {
  .btnSquareFill {
    height: 40px;
    line-height: 38px;
    font-size: $sub;
    letter-spacing: 1px;
    padding: 0 20px;
    box-shadow: 0 10px 30px #00000044;
    img {
      margin-top: -4px;
    }
  }
}

.toProfile {
  .btnSquare {
    height: 40px;
    line-height: 38px;
    font-size: $sub;
    letter-spacing: 1px;
    padding: 0 30px;
    box-shadow: 0 5px 20px rgba(0, 0, 0, 0.3);
  }
  img {
    margin-top: -3px;
    margin-right: 20px;
  }
}
