.HeaderNotif {
  background: #101b26;
  color: #ffff;
  font: 400 16px "NeulandGrotesk", Arial, Sans-serif;
  padding: 16px;
  line-height: 1.57;
  height: auto;
  letter-spacing: 0.5px;

  @media (max-width: 481px) {
    height: 64px;
    font: 400 14px "NeulandGrotesk", Arial, Sans-serif;
    letter-spacing: normal;
  }
}

.HeaderNotif .wrapFlex {
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1100px;
  margin: 0 auto;

  @media (min-width: 800px) {
    margin: 0 100px;
    max-width: 100%;
  }
}

.HeaderNotif .wrapFlex .message {
  text-align: left;
  flex: 1;
  line-height: 1.35;
}

.HeaderNotif .wrapFlex .icon {
  width: 50px;
}

@media (min-width: 481px) {
  .HeaderNotif .wrapFlex {
    justify-content: center;
    .message,
    .icon {
      flex: initial;
      width: auto;
      margin: 0 14px;
    }
  }
}

.HeaderNotif .link {
  color: #f2c94c;
  text-decoration: underline;
}

@keyframes ringing {
  0% {
    transform: rotate(-30deg);
  }
  1% {
    transform: rotate(0deg);
  }
  3% {
    transform: rotate(-28deg);
  }
  5% {
    transform: rotate(34deg);
  }
  7% {
    transform: rotate(-32deg);
  }
  9% {
    transform: rotate(0);
  }
  11% {
    transform: rotate(-28deg);
  }
  13% {
    transform: rotate(26deg);
  }
  15% {
    transform: rotate(-24deg);
  }
  17% {
    transform: rotate(22deg);
  }
  19% {
    transform: rotate(-20deg);
  }
  21% {
    transform: rotate(18deg);
  }
  23% {
    transform: rotate(-16deg);
  }
  25% {
    transform: rotate(14deg);
  }
  27% {
    transform: rotate(-12deg);
  }
  29% {
    transform: rotate(10deg);
  }
  31% {
    transform: rotate(-8deg);
  }
  33% {
    transform: rotate(6deg);
  }
  35% {
    transform: rotate(-4deg);
  }
  37% {
    transform: rotate(2deg);
  }
  39% {
    transform: rotate(-1deg);
  }
  41% {
    transform: rotate(1deg);
  }
  43% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(0);
  }
}

.HeaderNotif .iconRing {
  width: 32px;
  height: 32px;
  margin: 0 auto;
  background-image: url("/assets/notification-blue@2x.png");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;
  animation: ringing 3s 0.7s ease-in-out infinite;
  // transform-origin: 50% 4px;
  transform: rotate(-30deg);
}
