@import "zvariables.scss";

fieldset {
  display: block;
  border: none;
  padding: 0;
  position: relative;
  margin-top: 28px;
  margin-left: 0;
  margin-right: 0;

  & + & {
    margin-top: 28px;
  }

  &:first-child {
    margin-top: 0;
  }
}

.field__sml {
  & + & {
    margin-top: 9px;
  }
}

input,
textarea,
select {
  -moz-appearance: none;
  appearance: none;
  outline: none;
  &:focus {
    outline: none;
  }
}
select::-ms-expand {
  display: none;
}

input[type="number"] {
  -moz-appearance: textfield;
}

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

// title
// ===========================================
.formTitle {
  font-weight: 700;
  font-size: $h2 + 3;
  line-height: 1;
  letter-spacing: 0.5px;
  padding: 37px 20px 22px;
  & ~ .formSubtitle {
    margin-top: -10px;
  }
}
.formSubtitle {
  font-family: $main-alt-font;
  font-weight: 300;
  font-size: $ssub;
  line-height: $sub + 2;
  letter-spacing: 0.2px;
  padding: 0 20px;
  margin: 0;
  margin-bottom: 20px;
  max-width: 320px;
}

// Input
// ===========================================
.checkbox__wrapper {
  position: relative;
  cursor: pointer;

  span {
    width: auto;
    cursor: pointer;
  }
}

.form__controlWrapper {
  position: relative;
}

.form__control {
  display: block;
  width: 100%;
  border: 1px solid $fgrey;
  font-family: $main-alt-font;
  font-weight: 300;
  font-size: $pb;
  padding: 17.5px 19px;
  border-radius: 3px;
  color: $black;
  &:-webkit-autofill,
  &:-webkit-autofill:hover,
  &:-webkit-autofill:focus {
    background-color: initial;
  }

  &:focus {
    border: 1px solid $ffgrey;
  }

  &:disabled {
    background: #ededed;
    color: lighten($grey, 25);
    & ~ .form__pass-visible {
      background-color: transparent;
    }
  }

  &::placeholder {
    font-weight: 300;
    font-family: $main-alt-font;
    font-size: $ssub - 1;
    color: $form-color-placeholder;
    font-weight: 300;
  }

  &:-ms-input-placeholder {
    font-family: $main-font;
    color: $form-color-placeholder;
    font-weight: 300;
  }

  &::-ms-input-placeholder {
    font-family: $main-font;
    color: $form-color-placeholder;
    font-weight: 300;
  }

  &[type="number"]::-webkit-inner-spin-button,
  &[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
  &::-ms-clear {
    display: none;
  }
}

.form__controlLine {
  font-size: $sub;
  border: none;
  border-bottom: 1px solid $grey;
  text-align: center;

  &:focus {
    border: none;
    border-bottom: 1px solid $ffgrey;
  }

  &:disabled {
    background: darken($white, 5);
    color: lighten($grey, 25);
  }

  &::placeholder {
    font-size: $sub;
  }

  &:-ms-input-placeholder {
    font-size: $sub;
  }

  &::-ms-input-placeholder {
    font-size: $sub;
  }
}

.parsleyError {
  border-color: $main-red;
  &:focus {
    border-color: $main-red;
  }
  & ~ span {
    border-color: $main-red;
  }
}

.field__valid {
  position: absolute;
  width: 20px;
  height: 20px;
  right: 0;
  top: 0;
  // &-true {
  //   background: url(../images/checked.svg) no-repeat center / 70%;
  // }
  // &-false {
  //   background: url(../images/exclamation.svg) no-repeat center / 24%;
  // }
  // &-load {
  //   background: url(../images/spinner.svg) no-repeat center / 80%;
  //   animation: isRotate infinite 1s linear;
  // }
}

.form__controlChck {
  cursor: pointer;
  position: absolute;
  top: 0;
  left: 0;
  width: 25px;
  height: 25px;
  z-index: 3;
  opacity: 0;

  & + label {
    position: relative;
    padding: 0;
    width: 100%;

    span {
      display: inline-block;
      vertical-align: top;
      width: calc(100% - 40px);
      line-height: $h3;
      margin-left: -3px;
      margin-top: 3px;
      font-size: 14px;
    }

    * {
      font-family: $main-alt-font;
      font-weight: 300;
      color: $black;
    }

    a {
      display: initial;
      border-bottom: 2px solid $main-red;
      font-weight: 700;
      text-decoration: none;
      line-height: 1;
    }
  }

  & + label:before {
    content: "";
    margin-right: 12px;
    margin-top: 5px;
    display: inline-block;
    vertical-align: middle;
    width: 20px;
    height: 20px;
    background: $white;
    border: 3px solid #eaeaea;
    border-radius: 3px;
    box-sizing: border-box;
  }

  &:checked + label:before {
    background-color: $main-red;
  }

  & + .form__text {
    font-weight: 400;
  }
}

.form__controlChck__strong {
  @extend .form__controlChck;
  & + label {
    span {
      font-family: $main-alt-font;
      font-size: $p;
      font-weight: 700;
      letter-spacing: 0;
      line-height: $p + 1;
      color: #1a1a1a;
    }
  }
}

.form__controlChk__small {
  @extend .form__controlChck;
  & + label:before {
    width: 16px;
    height: 16px;
  }
  &:checked + label:before {
    line-height: 16px;
  }
}

.form__controlText {
  width: 100%;
  height: 150px;
  resize: none;
  padding: 16px;
}

.form__dropdown {
  position: relative;
  display: inline-block;
  width: auto;

  &:before {
    position: absolute;
    top: 50%;
    right: 8px;
    transform: translateY(-50%);
    content: "\f078";
    font-family: "Font Awesome 5 Free";
    font-size: $sml;
    font-weight: 900;
    color: $main-red;
    z-index: 2;
  }
}

.dp__block {
  display: block;

  &:before {
    right: 12px;
  }
}

.form__controlOpt {
  position: relative;
  background: $white;
  font-family: $main-font;
  font-weight: 700;
  font-size: $ssub;
  letter-spacing: 0.6px;
}

.form__controlOptAlt {
  font-size: $p;
  font-family: $main-alt-font;
  font-weight: 400;
  letter-spacing: -0.2px;
}

.form__controlDate {
  display: inline-block;
  width: auto;
  padding: 0 4px;
  padding-right: 24px;
  background: $white;

  & + & {
    margin-left: 8px;
  }
}

// Label
// ==========================================
.form__label {
  display: block;
  font-weight: 700;
  font-size: $ssub;
  color: $form-color-label;
  margin-bottom: 5px;
  letter-spacing: 0.7px;
  font-family: $font-face-primer;
}
.form__sublabel {
  font-family: $font-face-sekunder;
  font-size: 12px;
  text-decoration: underline;
  position: absolute;
  right: 0;
  top: 0;
}

.form__err {
  display: block;
  position: relative;
  > .parsleyErrorsList {
    list-style-type: none;
    padding: 0;
    margin: 0;
    margin-top: 4px;
    color: $main-red;
    font-size: $sml;
    * {
      font-family: $main-alt-font;
    }
  }
}

.form__warning {
  margin: 16px 0;
  color: $main-red;
}

.form__passVisible {
  position: absolute;
  top: 1px;
  right: 1px;
  height: 50px;
  line-height: 50px;
  padding: 0 12px;
  background: $white;
  border-radius: 0 3px 3px 0;
  border-left: none;
  &:before {
    content: "\f070";
    font-family: "Font Awesome 5 Free";
    color: $form-color-label;
  }
  &[data-visible="true"]:before {
    content: "\f06e";
  }
}

// Form Part
// =============================================
.form__footer {
  margin-top: 32px;
  .btnSquare {
    width: 100%;
    font-size: $sub + 2;
    height: 45px;
    line-height: 42px;
    letter-spacing: 1.7px;
    font-family: $font-face-primer;
    font-weight: 700;
    cursor: pointer;
    span {
      margin-top: 2px;
    }
  }
}

.form__footerExt {
  padding-bottom: 50px;
  .btnSquare {
    width: auto;
    padding: 0 42px;
  }
  .btnSquare,
  .btnSquare * {
    font-size: $sub;
    height: 45px;
    letter-spacing: 0;
    line-height: 42px;
  }
  .btnSquare img {
    height: 15px;
    margin-top: -4px;
  }
  .btnLink {
    font-size: $ssub;
    font-weight: 700;
    color: $form-color-value;
    cursor: pointer !important;
  }
  .btnLink.new {
    font-family: "NeulandGrotesk", Arial, sans-serif;
    color: #8895a9;
    text-decoration: none;

    &:hover {
      color: #515965;
    }

    &:active,
    &:focus {
      color: #515965;
      text-decoration: underline;
    }
  }
}

.form__footerNote {
  color: $main-grey;
  font-weight: 300;
  margin-top: 32px;
}

.form__passReq {
  list-style-type: none;
  margin: 16px 0 0;
  padding: 0;
  font-family: $main-alt-font;
  font-weight: 300;
  font-size: $p;
  color: $form-color-text;
  line-height: $h3;

  li {
    font-family: $main-alt-font;
    display: table-row;
  }

  li:before {
    display: table-cell;
    content: "";
    font-size: $ssub;
    text-align: right;
    padding-right: 8px;
    color: $form-color-text;
  }
}

.form__linkReg {
  vertical-align: middle;
  font-size: $sml;
  padding-left: 24px;

  label,
  a {
    display: block;
  }

  label + a {
    margin-top: 4px;
  }
}

.form__linkPass {
  margin-top: 12px;
}

.form__note {
  margin: 8px 0;
  font-size: $p;
  font-family: $main-alt-font;
  font-weight: 300;
  line-height: 1.3;
}
.fnCst {
  font-size: $p + 1;
  line-height: 24px;
}

.form__noteHigh {
  font-size: $ssub;
  line-height: 1.7;
}

.form__noteStrong {
  color: $main-grey;
  font-weight: 700;
}

.form__terms {
  p {
    font-family: $main-alt-font;
    font-weight: 300;
    font-size: $p - 1;
    line-height: $h3 - 2;
    margin: 0;
    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }
  padding: 9px 13px;
  margin-top: 15px;
  border: 2px solid #dedede;
  border-radius: 5px;
  max-height: 300px;
  overflow-y: scroll;
}

// Error Message
// =======================================
.err__msg {
  margin-bottom: 24px;
  max-width: 300px;
  color: $main-red;
  font-weight: 700;
}
.err__msgBig {
  font-weight: 700;
  font-size: $h3 + 4;
  line-height: $h3;
  letter-spacing: 0.4px;
  color: #940000;
}

.err__block {
  font-weight: 500;
  text-align: center;
  margin: 8px 0;
  padding: 4px;
  color: $white;
  background: $main-red;
  border-radius: 20px;
}

.prefBox {
  padding: 30px 24px;
  margin: 0 20px;
  background-color: $white;

  &:not(:last-child) {
    margin: 0 20px 30px;
  }
}

.jsPassList {
  li,
  li:before {
    color: $main-red !important;
  }
  li[data-passed="true"],
  li[data-passed="true"]:before {
    color: inherit;
  }
}

.authTitle {
  font-weight: 700;
  font-size: $h2 + 3;
  line-height: $h3 + 1;
  padding: 37px 0 36px;
  position: relative;
  z-index: -1;
  box-shadow: inset 0 -15px 15px -16px #00000022;
  color: $black;
}
.authForm {
  padding: 30px 30px 40px;
  position: relative;
  strong {
    font-family: $main-alt-font;
  }
  p {
    font-family: $main-alt-font;
    font-weight: 300;
    line-height: $h3;
  }
  .btnSquare {
    width: 100%;
    padding: 0;
    font-size: $sub;
  }
  .birth {
    display: flex;
    justify-content: space-between;
    flex: 1 3 2;
  }
  .form__dropdown:not(:last-child) {
    margin-right: 5px;
  }
  .form__dropdown:before {
    right: 10px;
  }
  .form__controlDate {
    padding: 0;
    padding-left: 14px;
    padding-right: 30px;
    height: 32px;
    line-height: 30px;
    border-radius: 100px;

    & option:disabled {
      color: $form-color-placeholder;
      font-weight: 300;
    }
  }
  .form__footer p {
    font-size: 12px;
  }
  .form__footer .btnSquare,
  .form__footer .btnSquareFill {
    letter-spacing: 0.97px;
    font-size: $sub + 2;
  }
  .cst {
    padding-bottom: 20px;
    border-bottom: 1px solid #cdcdcd;
    p {
      font-size: 14px;
    }
    a {
      font-size: $sub;
      font-weight: 700;
    }
  }
}

.loadbar {
  position: fixed;
  width: 100%;
  height: 5px;
  z-index: 12;
  overflow: hidden;
  display: none;
}
.loadbarTrue {
  display: block;
  & ~ .container {
    &:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: #ffffff77;
    }
  }
  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 5px;
    background-color: $main-red;
    animation: loadbar 1s infinite linear;
  }
  @keyframes loadbar {
    from {
      transform: translateX(-100%);
    }
    to {
      transform: translateX(100%);
    }
  }
}

.err__msg.big {
  font-size: 28px;
  font-weight: 700;
  line-height: 0.8;
  letter-spacing: 0.4px;
  color: #940000;
}

.voucher-form {
  margin-top: 10px;
  .form__control {
    padding: 0 14px;
    height: 54px;
    width: calc(100% - 35px);
    border: 6px solid #ebebeb;
    border-right: 0;
    font-size: $ssub;
    font-weight: 700;
  }
  .btn-img {
    width: 50px;
    height: 54px;
    position: absolute;
    right: 0;
    top: 0;
    border: 6px solid #ebebeb;
    border-left: 0;
    background-color: $main-red;
    img {
      margin: auto;
    }
  }
}

.pf__modal {
  color: #000;
  margin-left: 66px;
  position: relative;
}
