@import "../../scss/variables/zvariables";

.InputField {
  position: relative;
  margin: 6px 0;
}

.InputField input {
  display: block;
}

.errorField {
  font: 400 12px "NeulandGrotesk";
  color: $main-red;
  margin: 10px;
}

.InputField .inputWrapper {
  position: relative;
}

.InputField .inputWrapper .area {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(-35%, -50%);
  width: 36px;
  height: 36px;
  transition: 0.15s cubic-bezier(0.785, 0.135, 0.15, 0.86);

  & > button,
  div {
    transition: 0.15s cubic-bezier(0.785, 0.135, 0.15, 0.86);
  }

  &.allow,
  &.disallow {
    background-color: rgba(229, 233, 242, 0.5);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 80%;
    border-radius: 6px;
  }

  &.allow {
    background-image: url("/assets/green-check.svg");
  }

  &.disallow {
    background-image: url("/assets/red-close.svg");
  }
}

.InputField .inputWrapper .toggleShow {
  padding: 0;
  background-color: rgba(229, 233, 242, 0.5);
  // background-image: url("/assets/eye-dark.svg");
  // background-position: center center;
  // background-repeat: no-repeat;
  // background-size: 80%;
  border: 2px solid transparent;
  font-size: 0;
  color: transparent;
  cursor: pointer;
  border-radius: 6px;
  text-align: center;

  &:hover {
    background-color: rgba(229, 233, 242, 1);
  }

  &:focus,
  &:active {
    border-color: #515965;
  }

  &.show {
    // background-image: url("/assets/eye-crossed.svg");
    background-color: rgba(81, 89, 101, 0.7);

    &:hover {
      background-color: rgba(81, 89, 101, 1);
    }

    &:focus,
    &:active {
      border-color: #e5e9f2;
    }
  }

  img {
    display: block;
    margin: 0 auto;
    max-width: 80%;
  }
}

.InputField .inputWrapper .inputLoading {
  border-radius: 6px;
  background-color: rgba(229, 233, 242, 0.5);
  display: flex;
  align-items: center;
}
