.ButtonReg {
  appearance: none;
  -webkit-appearance: none;
  padding: 16px;
  text-align: center;
  width: auto;
  border-radius: 8px;
  background: #d7182a;
  border: 2px solid #d7182a;
  color: #ffffff;
  font-size: 15px;
  font: 700 15px "NeulandGrotesk", Arial, Helvetica, Sans-serif !important;
  line-height: 1.6;
  margin: 10px;
  letter-spacing: 0.65px;
  cursor: pointer;
  transition: 0.05s linear;
  text-transform: uppercase;
  -webkit-tap-highlight-color: transparent;
  max-width: calc(100% - 20px);
  width: 100%;
  display: block;

  &:hover {
    background: #c41628;
    color: #ffffff;
  }

  &:active {
    box-shadow: inset 1px 2px 7px rgba(0, 0, 0, 0.4);
    background: #c41628;
    color: #ffffff;
  }

  &:disabled,
  &.disabled {
    background: #646c79;
    border-color: #646c79;
    cursor: not-allowed;
  }

  &.secondary {
    color: #515965;
    border-color: #cacaca;
    background: #fff;
    box-shadow: none;

    &:hover {
      background: #efefef;
    }

    &:active,
    &:focus {
      border-color: #a9a9a9;
      box-shadow: inset 0 1px 10px rgba(0, 0, 0, 0.4);
    }
  }

  &.outline {
    background: #ffffff;
    color: rgba(16, 27, 38, 1);

    &:active {
      color: rgba(16, 27, 38, 0.8);
      border: 2px solid rgba(215, 24, 42, 0.6);
    }

    &:focus {
      border: 2px solid rgba(215, 24, 42, 0.6);
    }
  }

  &.inline {
    display: inline-block;
    width: auto;
  }

  &.textRed {
    color: #d7182a;
  }

  &.shadow {
    background-color: #fff;
    border: none;
    box-shadow: 0px 0px 11px 3px #00000014;

    &:active {
      box-shadow: 0px 0px 11px 3px #e700004f;
    }

    &:focus {
      box-shadow: 0px 0px 11px 3px #e700004f;
    }
  }

  &.condensed {
    font-family: "NeulandGrotesk-Condensed" !important;
  }

  &.f18 {
    font-size: 18px !important;
  }

  &.withShadow {
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, 0.25);
  }

  img {
    transform: translate(0, 30%);
    margin: 0 4px;
    display: none;
  }
}

.linkRedLine {
  appearance: none;
  -webkit-appearance: none;
  -webkit-tap-highlight-color: transparent;
  font: 700 15px "NeulandGrotesk-Condensed";
  display: inline-block;
  position: relative;
  color: #000;

  &::after {
    content: "";
    position: absolute;
    height: 2px;
    width: 100%;
    left: 0;
    bottom: 0;
    transform: translate(0, 100%);
    background: #d7182a;
  }

  &:active {
    color: #d7182a;
  }
}
