// Modal Component
// ===================================
.modal {
  overflow-y: auto;
}

.modalContent {
  border-radius: 0;
  margin-top: 60px;
}

.modalHeader {
  position: absolute;
  border-bottom: none;
  padding: 0;
  margin-top: -40px;
  right: 0;
}

.modalBorder {
  border-top: 8px solid $main-red;
}

.modalNav {
  padding: 0;
  padding-top: 24px;
}

// Modal Header
// ===================================
.modalTitle {
  font-size: $h3 + 4;
  font-weight: 700;
  padding: 20px;
  text-align: center;
  background-color: #f5f5f5;
  box-shadow: inset 0 -2px 5px -2px #ddd;
}

.modalSubtitle {
  font-size: $h3 + 4;
  margin-top: 20px;
}

.modalClose {
  position: absolute;
  top: 0;
  right: 0;
  width: 40px;
  height: 40px;
  color: $white;
  background-color: $main-red;
  background-image: url("/assets/close.svg");
  background-repeat: no-repeat;
  background-size: 40%;
  background-position: center;
}

// Modal Body
// ==================================
.modalBody {
  padding: 0;
}
.modalWrapper {
  padding: 20px;
}
.modalText {
  font-family: $main-alt-font;
  font-weight: 300;
  font-size: $ssub;
  line-height: $h3;
  margin: 12px 0;
}

.modal .btnSquare {
  height: 40px;
  width: 100%;
  line-height: 38px;
  font-size: $sub;
  img {
      margin-top: -3px;
  }
}

.modalNote {
  font-weight: 700;
  letter-spacing: 0.9px;
  color: #606060;
}