.Header {
  position: relative;
  height: 64px;
}

.Header .wrapper {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 22px 15px;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 10;
  background: #fff;
  box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.06);

  * {
    font: 700 20px "NeulandGrotesk-Condensed";
    color: #000;
  }
}

.wrapper .leftWrapper {
  display: flex;
  grid-gap: 100px;
}

.Header .home {
  width: auto;
  display: inline-block;
  color: #c61b00;

  a {
    color: inherit;
    text-decoration: none;
  }

  &:hover,
  &:active {
    text-decoration: underline;
  }

  &:focus {
    color: #333;
  }
}

.Header .midLink {
  display: flex;
  align-items: center;
  display: none;

  a {
    margin: 0 25px;
    color: #000;
  }

  a:focus {
    color: #000;
  }

  a:hover,
  a:active {
    text-decoration: underline;
    color: #c61b00;
  }

  @media (min-width: 1024px) {
    display: block;
  }
}

.Header .rightSide {
  display: flex;
  align-items: center;

  .regLog {
    line-height: 1;
    padding: 0 3px;
  }

  .regLog:focus {
    color: #000;
  }

  .regLog:hover,
  .regLog:active {
    color: #c61b00;
    text-decoration: underline;
  }

  .burger {
    position: relative;
    color: transparent;
    font-size: 0;
    margin-left: 25px;
    appearance: none;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;
    background: transparent;
    padding: 0;
  }

  .burger div {
    width: 24px;
    height: 3px;
    background: #7d8688;
    margin: 4px;
    transition: 0.35s ease-in-out;
  }

  .burger.active div:nth-child(1) {
    transform: translate(0, 100%) rotate(-45deg);
  }

  .burger.active div:nth-child(2) {
    transform: translate(0, -100%) rotate(45deg);
  }

  @media (min-width: 1024px) {
    .burger {
      display: none;
    }

    .regLog {
      padding: 0 6px;
    }

    .regLog:nth-child(1) {
      padding-left: 25px;
      border-left: 2px solid #e0e4e7;
    }
  }
}

.Header .wrapperFixed {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: 0.25s ease-in-out;
  opacity: 0;
  pointer-events: none;
  z-index: 99;

  .menu {
    width: 50%;
    max-width: 320px;
    background: #fff;
    position: absolute;
    top: 0;
    right: -100%;
    height: 100%;
    transition: 0.35s ease-in-out;
  }

  .menu .menuList {
    position: relative;
  }

  .menu .menuList a {
    display: block;
    color: #7d8688;
    padding: 20px;
    font: 400 14px "NeulandGrotesk";
    border-bottom: 1px solid #d7d7d7;
    appearance: none;
    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;
  }

  .menu .menuList a:hover {
    background: #f7f7f7;
    color: #585f5f;
  }

  &.active {
    opacity: 1;
    pointer-events: all;
    background: rgba(0, 0, 0, 0.7);
  }

  &.active .menu {
    transition-delay: 0.15s;
    right: 0;
  }

  .menu .buttonSet {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 20px 10px;
  }
}

.Header.transparent {
  height: 0;
}

.Header.transparent .wrapper {
  background: transparent;
  box-shadow: none;

  * {
    color: #fff;
    text-shadow: 0 1px 4px rgba(0, 0, 0, 0.15);
  }
}

.Header.transparent .rightSide {
  .burger div {
    background: #fff;
    box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
  }

  @media (min-width: 1024px) {
    .regLog {
      border-color: #fff;
    }
  }
}
