@import "../../scss/global.scss";

.sideMenuPoint_container {
  display: flex;
  flex-direction: row;
  align-content: space-between;
  color: #fff;
  padding: 15px;
  background-color: #d7182b;

  .point {
    font-size: 32px;
    line-height: 22px;
    color: #fff;
    font-weight: bold;

    .poin {
      font-size: 20px;
      font-family: $font-face-primer;
    }

    .reedem {
      appearance: none;
      -webkit-appearance: none;
      background: transparent;
      font-size: 15px;
      font-family: $font-face-sekunder;
      color: #fff;
      padding: 0;
      display: block;
    }

    .reedem:hover,
    .reedem:focus {
      text-decoration: underline;
    }

    .reedem:disabled {
      opacity: 0.6;
      cursor: not-allowed;
    }
  }
}

.navLink_container {
  display: flex;
  flex-direction: column;
  position: relative;
  height: 100%;

  .navLink_link {
    position: absolute;
    top: 1%;
    width: 100%;

    .navLinkLink {
      font-family: $font-face-sekunder;
      font-size: 15px;
      font-style: normal;
      color: #101b26;
      padding: 18px 20px;
      border-bottom: 1px solid #d3dce6;
      width: 100%;
      text-align: left;
    }
  }

  .navLink_linkLogin {
    .navLinkLink {
      font-family: $font-face-sekunder;
      font-size: 15px;
      font-style: normal;
      color: #101b26;
      padding: 18px 20px;
      border-bottom: 1px solid #d3dce6;
      width: 100%;
      text-align: left;
    }

    .navLinkLink_bg {
      background-image: url(/assets/BaseNavbar.png);
      background-size: cover;
      background-repeat: no-repeat;
      font-family: $font-face-tersier;
      font-size: 19px;
      font-style: normal;
      color: #101b26;
      padding: 10px 14px;
      background-position: center;
      border-bottom: 1px solid #d3dce6;
      width: 100%;

      span {
        font-family: $font-face-tersier;
        color: #d7182a;
      }

      img {
        max-width: 100%;
        max-height: 40px;
      }
    }
  }
}

.navLink_btn {
  width: 100%;
  margin: 0 auto;
  position: absolute;
  bottom: 1%;
  left: 0;
  right: 0;
}

.navLink_btn > .navLinkBtn {
  border: 1px solid #ccc;
  border-radius: 10px;
  font-size: 15px;
  font-weight: bold;
  line-height: 1.6;
  color: #d7182a;
  width: 80%;
  padding: 14px 20px;
  margin: 10px auto;
  font: 700 15px "NeulandGrotesk";
  display: block;
  text-align: center;

  &.red {
    background: #d7182a;
    color: #eeeeee;
  }
}

.point {
  display: flex;
  flex-direction: row;

  img {
    margin-right: 10px;
  }
}

.burger {
  cursor: pointer;
  position: absolute;
  right: 28px;
  top: 23px;
  width: 20px;

  .burgerBar1,
  .burgerBar2,
  .burgerBar3 {
    width: 18px;
    height: 2.5px;
    background-color: #000;
    margin: 4px 0;
    transition: all 0.5s;
  }
}
