@import "../../scss/global.scss";

.sideNavWrapper {
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 10;
  top: 60px;
  bottom: 60px;
  display: none;
}

.sideNavWrapper_desktop {
  position: fixed;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 10;
  top: 60px;
  bottom: 60px;
  display: none;
}

.sideNav {
  height: 100vh;
  width: 270px;
  position: absolute;
  z-index: 10;
  top: 0px;
  right: 0;
  background-color: #f9faff;
  overflow-x: hidden;
  box-shadow: 0 2px 10px -1px rgba(118, 118, 118, 0.5);
  transition: 0.25s cubic-bezier(0.645, 0.045, 0.355, 1);
  transform: translateX(150%);

  &.show {
    transform: translateX(0%);
  }

  .sideNav_container {
    display: flex;
    flex-direction: column;
    height: 100%;

    .sideMenu_bg {
      position: absolute;
      top: 0;
    }

    .sideMenuPoint_container {
      display: flex;
      flex-direction: row;
      align-content: space-between;
      color: #fff;
      padding: 20px;
      background-color: #d7182b;

      .icon_point {}

      .pointContainer_wrapper {
        display: flex;
        flex-direction: column;
        margin-left: 16px;
        width: 100%;

        .point {
          font-size: 32px;
          line-height: 34px;
          color: #fff;
          font-weight: bold;
        }
      }

      .redeemPoin_btn {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        width: 100%;
      }

      .redeemPoin_btn span {
        font-size: 16px;
        line-height: 19px;
        font-family: "NeulandGrotesk", Arial, Helvetica, sans-serif;
        color: #fff;
      }
    }

    .navLink_container {
      // padding: 0 10%;
      display: flex;
      flex-direction: column;
      position: relative;
      height: 100%;

      .navLink_bg {
        padding: 7%;
      }

      .navLink_bgImg {
        background-image: url("/assets/Base2222.png");
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        padding: 7%;
      }
    }

    .navLink_link {
      position: absolute;
      top: 1%;
      width: 100%;

      .navLinkLink {
        font-family: $font-face-sekunder;
        font-size: 15px;
        font-style: normal;
        color: #101b26;
        padding: 18px 20px;
        // border-top: 1px solid #d3dce6;
        border-bottom: 1px solid #d3dce6;
        width: 100%;
      }
    }

    .navLink_linkLogin {
      .navLinkLink {
        font-family: $font-face-sekunder;
        font-size: 15px;
        font-style: normal;
        color: #101b26;
        padding: 18px 20px;
        // border-top: 1px solid #d3dce6;
        border-bottom: 1px solid #d3dce6;
        width: 100%;
      }

      .navLinkLink_bg {
        background-image: url(/assets/BaseNavbar.png);
        background-size: cover;
        background-repeat: no-repeat;
        font-family: "Crushed Stone Regular", Arial;
        font-size: 19px;
        font-style: normal;
        color: #101b26;
        padding: 14px 20px;
        // border-top: 1px solid #d3dce6;
        border-bottom: 1px solid #d3dce6;
        width: 100%;

        span {
          font-family: $font-face-tersier;
          color: #d7182a;
        }
      }
    }

    .navLink_line {
      width: 100%;
      margin: unset;
      border: 0.5px solid #ccc;
    }

    .navLink_btn {
      width: 100%;
      margin: 0 auto;
      position: absolute;
      bottom: 10%;
      left: 0;
      right: 0;
    }

    .navLink_btn>.navLinkBtn {
      border: 1px solid #ccc;
      border-radius: 10px;
      font-size: 15px;
      font-weight: bold;
      line-height: 1.6;
      color: #d7182a;
      width: 80%;
      padding: 14px 20px;
      margin: 10px auto;
      font: 700 15px "NeulandGrotesk";
      display: block;
      text-align: center;

      &.red {
        background: #d7182a;
        color: #eeeeee;
      }
    }
  }
}

.bg_pattern {
  bottom: 10px;
  position: absolute;
  mask-image: linear-gradient(to top, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0.01));
}

.border {
  bottom: 0;
  position: absolute;
  background-color: #3b3b3b;
  height: 10px;
  width: 100%;
}

/* The navigation menu links */
.navLink a {
  padding: 16px 8px 16px 32px;
  text-decoration: none;
  font-size: 16px;
  line-height: 19px;
  color: #000;
  font-family: "NeulandGrotesk", Arial, Helvetica, sans-serif;
  display: block;
  transition: 0.3s;
  z-index: 1;
}

.sideNav .logout_btn {
  display: flex;
  justify-self: flex-end;
  color: #d7182b;
  padding: 16px 8px 16px 32px;
  text-decoration: none;
  font-size: 16px;
  line-height: 19px;
  margin-bottom: 16px;
  font-family: "NeulandGrotesk", Arial, Helvetica, sans-serif;
}

/* When you mouse over the navigation links, change their color */
.sideNav a:hover {
  color: #d7182b;
}

.point {
  display: flex;
  flex-direction: row;

  img {
    margin-right: 10px;
  }
}

.burger {
  cursor: pointer;
  position: absolute;
  right: 28px;
  top: 23px;
  width: 20px;

  .burgerBar1,
  .burgerBar2,
  .burgerBar3 {
    width: 18px;
    height: 2.5px;
    background-color: #000;
    margin: 4px 0;
    transition: all 0.5s;
  }
}

.active .burgerBar1 {
  transform: rotate(-40deg) translate(-5px, 4px);
  width: 27px;
  height: 3px;
}

.active .burgerBar2 {
  opacity: 0;
}

.active .burgerBar3 {
  transform: rotate(40deg) translate(-6px, -5px);
  width: 27px;
  height: 3px;
}

/* On smaller screens, where height is less than 450px, change the style of the sidenav (less padding and a smaller font size) */
@media screen and (max-height: 450px) {
  .sideNav {
    padding-top: 15px;
  }

  .sideNav a {
    font-size: 18px;
  }
}

.mobileNav__backdrop {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  transition: 0.3s;
  pointer-events: none;

  &.isOpen {
    pointer-events: all;
    background-color: rgba(16, 27, 38, 0.8);
  }
}

.mobileNav {
  // width: 250px;
}

.mobileNav__point {
  padding: 14px 20px;
  color: #fff;
  font-size: 15px;
  background-color: #d7182a;
  display: flex;
  align-items: center;

  span {
    font-family: $font-face-sekunder;
  }
}

.mobileNav__pointn {
  font-size: 22px;
  margin-bottom: 4px;
}

.mobileNav__icon {
  margin-right: 20px;
}

.mobileNav__link {
  display: block;
  width: 100%;
  padding: 18px 20px;
  font-family: $font-face-sekunder;
  font-size: 15px;
  color: #101b26;
  box-shadow: 0 1px 0 0 #d3dce6;
  background: transparent;
  text-align: left;
}

.mobileNav__bottom {
  position: absolute;
  bottom: 80px;
  right: 20px;
  left: 20px;
}

.mobileNav__button {
  display: block;
  width: 100%;
  padding: 16px 0;
  text-align: center;
  border-radius: 8px;
  font-size: 15px;
  background-color: #d7182a;
  color: #ffffff;
  font-family: $font-face-sekunder;
  font-weight: bold;

  &+& {
    margin-top: 16px;
  }
}

.mobileNav__button--ghost {
  border: 2px solid #d3dce6;
  background-color: #fff;
  color: #515965;
}