@import "../../scss/global.scss";

.headerOuter {
  height: 56px;
}

.headerOuter.hasNotif {
  height: 114px;
}

.headerContent {
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: space-between;
  align-items: center;

  .pointHeaderContainer {
    display: flex;
    flex-direction: row;
    align-items: center;

    .point {
      font-family: $font-face-primer;
      font-size: 20px;
      font-weight: bold;
      letter-spacing: 1.3px;
      color: #000000;
      margin: 0 10px;
    }

    &:disabled {
      cursor: not-allowed;
      opacity: 0.6 !important;

      * {
        opacity: 0.6 !important;
      }
    }
  }
}

@media only screen and(min-width: 800px) {
  .headerNav {
    width: 100%;
    display: block;
  }

  .headerOuter {
    height: 60px;
  }

  .headerOuter.hasNotif {
    height: 124px;
  }
}

@media only screen and(max-width: 799px) {
  .headerNav {
    width: 100%;
    display: block;
  }
}

@media only screen and(min-width: 800px) {
  .headerWrapper {
    display: none;
  }

  .header__linkAccount::after {
    content: none;
  }

  .headerWrapperDesktop {
    display: block;
    background-color: #fff;
    box-shadow: 0 4px 14px -7px rgba(101, 101, 101, 0.5);
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;

    .headerContainer {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 100%;
      margin: auto;
      padding: 0 110px;
      height: 60px;

      @media only screen and (max-width: 799px) {
        padding: 0 15px;
      }

      .headerLeft {
        display: flex;
        grid-gap: 100px;
      }

      .logoMarlboro {
        img {
          width: 105px;
        }
      }

      .midLink {
        display: flex;
        align-items: center;
        font: 700 20px "NeulandGrotesk-Condensed";
        color: #000;

        a {
          margin: 0 25px;
          color: #000;
        }

        a:hover {
          color: #c61b00;
        }

        a:active,
        a:focus {
          text-decoration: underline;
        }
      }

      .usernamePoint {
        display: flex;
        flex-direction: row;
        align-items: center;
        border-left: 2px solid #e0e4e7;
        padding-left: 20px;

        .beranda {
          font-size: 20px;
          font-weight: bold;
          letter-spacing: 0.7px;
          color: #000000;
          margin-right: 30px;
          cursor: pointer;
          transition: all 0.2s ease;

          &:hover {
            color: #c61b00;
            text-decoration: none;
          }
        }

        .usernameContainer {
          display: flex;
          flex-direction: row;
          align-items: center;

          &:hover {
            .username {
              color: #c61b00;
            }
          }

          .iconAccount {
            width: 18px;
          }

          .username {
            font-size: 20px;
            font-weight: bold;
            letter-spacing: 0.7px;
            color: #d7182a;
            transition: all 0.2s ease;
            margin: 0 5px;
            font-family: "NeulandGrotesk-Condensed";
          }

          .arrowDown {
            font-size: 11px;
            color: #d7182a;
          }
        }

        .pointHeaderContainer {
          display: flex;
          flex-direction: row;
          align-items: center;
          margin-left: 20px;

          .point {
            font-family: "NeulandGrotesk", Arial, Helvetica, sans-serif;
            font-size: 20px;
            font-weight: bold;
            letter-spacing: 1.3px;
            color: #000000;
            margin-left: 10px;
            margin-right: 5px;
          }

          &:disabled {
            cursor: not-allowed;
            opacity: 0.6 !important;

            * {
              opacity: 0.6 !important;
            }
          }
        }
      }
    }
  }

  .headerNav.hasNotif .headerWrapperDesktop {
    height: auto;
  }
}

.headerContainerMarlboro {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  margin: auto;

  .logo-marlboro {
    width: 150px;
  }
}

@media only screen and(max-width: 799px) {
  .headerWrapperDesktop {
    display: none;
  }

  .headerWrapper {
    display: block;
    background-color: #fff;
    box-shadow: 0 4px 14px -7px rgba(101, 101, 101, 0.5);
    display: block;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10;

    .headerContainer {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 100%;
      max-width: 799px;
      height: 56px;
      margin: auto;
      padding: 0 15px;

      .logoMarlboro {
        img {
          width: 105px;
        }
      }

      .usernameMenu {
        display: flex;
        align-items: center;

        .username {
          font-size: 20px;
          letter-spacing: 0.74px;
          font-stretch: condensed;
          font-weight: bold;
          margin-right: 20px;
          display: none;
        }

        .burger {
          cursor: pointer;

          .burgerBar1,
          .burgerBar2 {
            width: 18px;
            height: 1.6px;
            background-color: #000;
            margin: 4px 0;
            transition: all 0.5s;
          }
        }

        .active .burgerBar1 {
          transform: rotate(-40deg) translate(-5px, 4px);
          width: 27px;
          height: 3px;
        }

        .active .burgerBar2 {
          opacity: 0;
        }
      }
    }
  }

  .headerNav.hasNotif .headerWrapper {
    height: auto;
  }
}

@media only screen and(min-width: 800px) and(max-width: 1024px) {
  .headerContainer {
    padding: 0 20px !important;
  }
}

.dropbtn {
  color: white;
  // padding: 16px 6px;
  font-size: 16px;
  border: none;
  cursor: pointer;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdownContent {
  font-family: "NeulandGrotesk-Condensed";
  display: none;
  position: absolute;
  background-color: #f9f9f9;
  min-width: 160px;
  z-index: 1;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 0.6px 9.2px 0 rgba(0, 0, 0, 0.06),
    0 2px 30.8px 0 rgba(0, 0, 0, 0.08), 0 9px 138px 0 rgba(0, 0, 0, 0.14);
}

.dropdownContent a,
.dropdownContent button {
  color: #d7182a;
  padding: 12px 10px;
  text-decoration: none;
  display: block;
  font-size: 18px;
  font-weight: 700;
  width: 100%;
  text-align: right;

  // & + a {
  //   border-top: solid 1px #cdc7c7;
  // }
}

.dropdownContent a:hover,
.dropdownContent button:hover {
  background-color: #f1f1f1;
  color: #c61b00;
}

.dropdown:hover .dropdownContent {
  display: block;
}

.logoName {
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 0.7px;
  text-align: right;
  color: #000000;
  margin-right: 20px;
}

.MLogo {
  position: relative;

  img {
    max-width: 100%;
  }
}
