@import "../scss/variables/zvariables";

.MPopup {
  position: relative;
  text-align: center;
  font-family: "NeulandGrotesk";

  * {
    font-family: "NeulandGrotesk", Arial, Helvetica, sans-serif;
  }

  .text {
    margin: 20px auto;
  }

  .text .title {
    font-weight: 700;
    font-size: 20px;
    margin: 0 auto 12px;
    line-height: 1.4;
    color: #101b26;
  }

  .text .desc {
    font-size: 15px;
    line-height: 1.73;
    color: #000000;
    margin: 12px auto;
  }

  .actionGroup {
    margin: 20px auto 0;
  }

  .actionGroup button {
    display: block;
    width: 100%;
    margin: 12px auto;
  }

  .actionGroup.addBottom {
    padding-bottom: 13px;

    @media (min-width: 768px) {
      padding-bottom: 70px;
    }
  }

  .timerSection {
    position: relative;
    display: flex;
    justify-content: space-between;
    color: $main-red;
    padding: 10px 0;

    .timer {
      position: relative;
      padding-left: 34px;

      &::before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate(10px, -50%);
        background-image: url("/assets/icon-stopwatch.svg");
        background-position: center center;
        background-repeat: no-repeat;
        background-size: contain;
        width: 24px;
        height: 24px;
      }
    }
  }

  .quizSection {
    background: #f9fafc;
    border: 1px solid #f9fafc;
    text-align: left;
    margin: 0 -24px;
    padding: 14px 24px 60px;

    @media (max-width: 768px) {
      padding: 14px 24px 100px;
      margin: 0 -24px -100px;
    }

    .quizTitle {
      margin: 10px auto;
      text-transform: uppercase;
      font: 700 15px "NeulandGrotesk", Arial, sans-serif;
    }

    .quizDesc {
      line-height: 1.73;
      margin: 12px auto;
      font: 400 15px "NeulandGrotesk", Arial, sans-serif;
    }

    .quizAction {
      margin: 12px auto;
    }
  }

  .MPopup__Point {
    position: relative;
    background-color: #ffeede;
    border: 1.2px solid #ffd7b4;
    border-radius: 30px;
    padding: 10px;
    font: 700 16px "NeulandGrotesk", Arial, sans-serif;
    letter-spacing: 0.5px;
    margin: 0 auto 12px;
    width: 130px;
    color: $main-red;
    text-align: right;
    padding-right: 15px;
    line-height: 1;

    &.wide {
      width: 145px;
      padding-right: 20px;
    }

    &::before {
      content: "";
      position: absolute;
      top: 50%;
      left: 20px;
      width: 30px;
      height: 30px;
      transform: translate(0, -50%);
      background-image: url("/assets/icon-point-red.svg");
      background-position: center center;
      background-repeat: no-repeat;
      background-size: contain;
    }

    &::after {
      content: "";
      position: absolute;
      top: 50%;
      left: 53px;
      width: 18px;
      height: 18px;
      transform: translate(0, -50%);
      background-image: url("/assets/red-plus-thick-ikonate.svg");
      background-position: center center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }

  .MPopup__textLink {
    text-transform: uppercase;
    text-decoration: underline;
    text-align: center;
    color: #555555;
    font-weight: 700;
    margin: 20px auto;
    margin: 0 auto;
    width: auto !important;
    cursor: pointer;
  }
}

.ReactModal__Content {
  transition: 0.25s ease-in-out;
  transform: translate(-50%, 0%);
}

@keyframes openModal {
  from {
    transform: translate(-50%, 0%);
  }
  to {
    transform: translate(-50%, -50%);
  }
}

.ReactModal__Content--after-open {
  animation: openModal 0.5s cubic-bezier(0.175, 0.885, 0.32, 1.275) 0.05s 1
    forwards;
}
