.optanon-show-settings-button.optanon-toggle-display {
  max-width: unset;

  .optanon-show-settings-left {
    display: none;
  }
  
  .optanon-show-settings-right {
    display: none;
  }

  .optanon-show-settings-middle {
    background: transparent !important;
    border: none !important;
    padding-top: 0 !important;
  }

  .optanon-show-settings-middle .optanon-show-settings.marlboro {
    font: 400 17px "NeulandGrotesk-Condensed" !important;
    letter-spacing: .6px;
  }
}


