@import "../../scss/variables/zvariables";

@keyframes rotate {
  from {
  }
  to {
    transform: rotate(360deg);
  }
}

.Loading {
  border-radius: 100%;
  border: 4px solid $main-red;
  animation: rotate 1s linear infinite;
  margin: 0 auto;
  width: 35px;
  height: 35px;
  border-top-color: transparent;
}

.Loading.small {
  width: 20px;
  height: 20px;
  border-width: 3px;
}

.Loading.big {
  width: 55px;
  height: 55px;
  border-width: 6px;
}

.Loading.large {
  width: 65px;
  height: 65px;
  border-width: 7px;
}

.Loading.white {
  border-color: #fff;
  border-top-color: transparent;
}

.LoadingContent {
  // background: #fff;
  min-height: 40vh;
  text-align: center;
  margin: 40px auto;
  position: relative;

  & > div {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
}
