.ghw_container {
  height: 60px;
}

.ghw_light {
  background: #fff;
  height: 60px !important;
}

.ghw {
  position: fixed;
  bottom: 0;
  display: block;
  width: 100%;
  height: 90px;
  background: #000;
  overflow: hidden;
  // z-index: 15;
  font-family: "NeulandGrotesk-Condensed", Arial, Helvetica, sans-serif;
  z-index: 9;

  @media (max-width: 380px) {
    height: 85px;
  }
}

.ghw__wrapper {
  position: relative;
  height: 100%;
  width: 100%;
  max-width: 480px;
  margin: auto;

  @media only screen and (min-width: 1024px) {
    max-width: unset;
  }
}

.ghw__icon_left {
  position: absolute;
  top: 50%;
  left: 5px;
  transform: translateY(-50%);
  height: 70%;
  max-width: 70px;
}

.ghw .ghw__icon_left {
  left: 10px;
  max-width: 90px;
}

.ghw__icon_right {
  position: absolute;
  top: 50%;
  right: 5px;
  height: 34px;
  width: 34px;
  font-size: 16px;
  line-height: 32px;
  transform: translateY(-50%);
  font-weight: 700;
  text-align: center;
  color: #fff;
  border: 1.5px solid #fff;
  border-radius: 100px;
}

@media (max-width: 340px) {
  .ghw__icon_right {
    top: 15px;
    width: 24px;
    height: 24px;
    font-size: 13px;
    line-height: 24px;
  }
}

.ghw__warning {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  text-align: center;
  width: 225px;
}

@media (max-width: 425px) {
  .ghw__warning {
    margin-left: 20px;
  }
}

@media (max-width: 360px) {
  .ghw__warning {
    margin-left: 19px;
  }
}

@media (max-width: 340px) {
  .ghw__warning {
    margin-left: 30px;
  }
}

.ghw__warning,
.ghw__warning * {
  font-size: 9px;
  font-weight: 700;
  letter-spacing: 0.5px;
  line-height: 1;
  color: #fff;
}

.ghw__warning span {
  display: block;
  font-size: 16px;
}

.ghw_light {
  background: #fff;
}

.ghw_light .ghw__icon_right {
  color: #000;
  border-color: #000;
}

.ghw_light .ghw__warning,
.ghw_light .ghw__warning * {
  color: #000;
}

.ghw_container {
  height: 60px;
}

.ghw__warning {
  width: unset !important;
}

@media only screen and (min-width: 520px) {
  .ghw__warning,
  .ghw__warning * {
    font-size: 12px !important;
  }
}

@media only screen and (min-width: 670px) {
  .ghw__warning,
  .ghw__warning * {
    font-size: 15px !important;
  }
}
