@import "../../scss/variables/zvariables";

.Popup {
  position: relative;
  padding-top: 0;
  padding-bottom: 0;
  padding-right: 0;
  padding-left: 0;
  transition: 0.35s ease-in-out;
  background: #fff;
  padding: 20px 24px;
  border-radius: 8px;
  // z-index: 2;

  .wrapper {
    position: relative;
  }

  .risingStar {
    position: absolute;
    top: 0;
    left: 50%;
    width: 165px;
    height: 98px;
    background-image: url("/assets/rising-star.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    transform: translate(-50%, -20%);
  }

  &.useRisingStar {
    padding-top: 55px;
  }

  @keyframes rising {
    0% {
      transform: translate(-50%, -20%);
    }
    100% {
      transform: translate(-50%, -33%);
    }
  }

  &.opened .risingStar {
    // animation: rising 1s cubic-bezier(0.68, -0.55, 0.265, 1.55) 2 forwards;
    animation: rising 1.5s linear 1s 1 forwards;
    // opacity: 1;
  }

  .btnClose {
    appearance: none;
    -webkit-appearance: none;
    padding: 0;
    margin: 0;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(25%, -75%);
    transition: 0.15s ease-in-out;
    width: 30px;
    height: 30px;
    background-image: url("/assets/ikonate-close.svg");
    // background-image: url("/assets/Base2222.png");
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    -webkit-tap-highlight-color: transparent;
    font-size: 0;
    color: transparent;
    cursor: pointer;
  }

  .btnClose:active {
    transform: translate(25%, -75%) scale(0.8);
  }

  &.useRisingStar .btnClose {
    transform: translate(25%, -25%);
  }

  @keyframes useScale {
    from {
      transform: scale(0);
    }
    to {
      transform: scale(1);
    }
  }

  &.useEnvelope {
    transform: translate(0, 30%);
  }

  @keyframes pullup {
    from {
      transform: translate(0, 30%);
    }
    to {
      transform: translate(0, 0%);
    }
  }

  @media (max-width: 481px) {
    &.useEnvelope {
      padding-bottom: 80px;
      transform: translate(0, 25%);
    }

    @keyframes pullup {
      from {
        transform: translate(0, 25%);
      }
      to {
        transform: translate(0, 0%);
      }
    }
  }

  &.opened.useEnvelope {
    animation: pullup 1.5s cubic-bezier(0.075, 0.82, 0.165, 1) 0.25s 1 forwards;
  }
}

.Popup.Alert {
  background-color: #000;
  color: #fff;
  border-radius: 30.5px;
  box-shadow: 0 5px 30px 0 rgba(0,0,0,0.3);
  padding: 14px 19px;
}

.envelope {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  // width: calc(100% + 20px);
  // height: 50%;
  width: 400px;
  height: 230px;
  transform: translate(-50%, 50%);
  // background-color: $main-red;
  background-image: url("/assets/popup-envelop.svg");
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;

  @media (max-width: 768px) {
    // width: calc(100% + 17px);
    // height: 40%;
    width: 338px;
    height: 200px;
  }
}

.ModalOpened {
  background: rgba(255, 0, 0, 0.5);
}
