.staticp {
  padding: 40px 23px 80px;
}
.staticp__title {
  font-size: $h2;
}
.staticp__subtitle {
  font-size: $h3 + 2;
  margin: 30px 0 0;
}
.staticp__text {
  font-family: $main-alt-font;
  font-weight: 300;
  line-height: $h3 + 1;
  margin-top: 0;
  strong {
    font-family: $main-alt-font;
    font-weight: 700;
  }
}
.staticp__list {
  padding-left: 16px;
  margin-top: 0;
  .staticp__list,
  & .staticp__listAlpha {
    padding-left: 16px;
  }
  &--alpha {
    list-style-type: lower-alpha;
  }
}
.staticp__list li {
  font-family: $main-alt-font;
  font-weight: 300;
  line-height: $h3 + 1;
}
.staticp__btnLink {
  font-family: "NeulandGrotesk", Arial, Helvetica, sans-serif;
  font-weight: 700;
  color: #000;
  text-decoration: none;
  border-bottom: 3px solid #c61b00;
  line-height: 1;
}
