@import "../../scss/variables/fonts.scss";

.footerWrapper {
  background-color: #000;
  width: 100%;
  height: auto;
  color: #fff;
  padding: 24px 0px;
  // position: fixed;
  // bottom: 106px;
  @media only screen and(max-width: 720px) {
    // bottom: 126px;
  }
  @media only screen and (min-width: 800px) {
    .footerNavContainer {
      display: flex;
      justify-content: space-between;
      color: #fff;
      font-size: 17px;
      letter-spacing: 0.6px;
      font-family: $font-face-primer;
      font-weight: 400;
      max-width: 80%;
      margin: auto;
      .footerNavlink {
        margin-bottom: 30px;
        cursor: pointer;
        color: #f9faff;
        &:hover {
          color: #c61b00;
        }
      }
    }
  }
  @media only screen and (min-width: 481px) and (max-width: 799px) {
    .footerNavContainer {
      display: flex;
      justify-content: space-between;
      color: #fff;
      font-size: 17px;
      letter-spacing: 0.6px;
      font-family: $font-face-primer;
      max-width: 90%;
      margin: auto;
      .footerNavlink {
        margin-bottom: 30px;
        cursor: pointer;
        color: #f9faff;
        width: 100%;
        text-align: center;
        &:hover {
          color: #c61b00;
        }
      }
    }
  }

  .copyright {
    font-size: 22px;
    text-align: center;
    letter-spacing: 0.8px;
    font-family: $font-face-primer;
    font-weight: 400;
  }

  &.mfb {
    display: none;
  }
}

@media only screen and(max-width: 480px) {
  .footerWrapper {
    background-color: #000;
    width: 100%;
    height: auto;
    color: #fff;
    .footerNavContainer {
      display: flex !important;
      justify-content: space-between;
      color: #fff;
      font-size: 17px;
      letter-spacing: 0.6px;
      font-family: $font-face-primer;
      max-width: 480px;
      margin: auto;
      .navLeft {
        display: flex;
        flex-direction: column;
        margin: 20px 15px 0;
        .footerNavlink {
          margin-bottom: 0px;
          cursor: pointer;
          color: #f9faff;
          &:hover {
            color: #c61b00;
          }
        }
        .footerNavlink + .footerNavlink {
          margin-top: 15px;
        }
      }
      .navRight {
        display: flex;
        flex-direction: column;
        margin: 20px 15px 0;
        .footerNavlink {
          margin-bottom: 0;
          cursor: pointer;
          color: #f9faff;
          &:hover {
            color: #c61b00;
          }
        }
        .footerNavlink + .footerNavlink {
          margin-top: 15px;
        }
      }
    }
    .copyright {
      font-size: 22px;
      text-align: center;
      letter-spacing: 0.8px;
      font-family: $font-face-primer;
    }
  }
}

.container_ {
  &nav_footer {
    display: flex;
    width: 100%;
    justify-content: center;
    display: flex;
    justify-content: space-between;
    color: #fff;
    font-size: 17px;
    letter-spacing: 0.6px;
    font-family: $font-face-primer;
    font-weight: 400;
    max-width: 80%;
    margin: auto;
    @media only screen and(max-width: 720px) {
      flex-direction: row;
      max-width: unset;
    }
  }
}
.wrapper_ {
  &nav_left {
    display: flex;
    flex-direction: row;
    width: 100%;
    display: flex;
    color: #fff;
    font-size: 17px;
    letter-spacing: 0.6px;
    font-family: $font-face-primer;
    font-weight: 400;
    max-width: 70%;
    justify-content: space-around;
    @media only screen and(max-width: 720px) {
      width: 100%;
      margin: 20px 15px 0;
      flex-direction: column;
    }
  }
  &nav_right {
    display: flex;
    flex-direction: row;
    width: 100%;
    color: #fff;
    font-size: 17px;
    letter-spacing: 0.6px;
    font-family: $font-face-primer;
    font-weight: 400;
    max-width: 70%;
    justify-content: space-around;
    @media only screen and(max-width: 720px) {
      width: 100%;
      margin: 20px 15px 0;
      flex-direction: column;
      align-items: flex-end;
      justify-content: unset;
    }
  }
}

.nav {
  margin-right: 8px;
  margin-bottom: 30px;
  cursor: pointer;
  color: #f9faff;

  &:last-child {
    margin-right: 0;
  }

  &:hover {
    color: #d7182a;
    text-decoration: underline;
  }

  @media only screen and(max-width: 720px) {
    margin-right: unset;
  }
}

.websiteIni {
  text-align: center;
  letter-spacing: 0.75px;
  font: lighter 12px $font-face-primer;
  background-color: #c5d8d9;
  line-height: 1.3;
  text-align: center;
  padding: 12px 24px;
  z-index: 1;
  color: #000;
  // position: fixed;
  // bottom: 61px;
  // width: 100%;

  @media (min-width: 768px) {
    font-size: 14px;
  }

  @media (min-width: 1024px) {
    font-size: 16px;
  }

  &.mfb {
    position: fixed;
    bottom: 85px;
    left: 0;
    right: 0;
    width: 100%;
    background-color: #131313;
    color: #ececec;
  }
}
