.ButtonReg {
  appearance: none;
  -webkit-appearance: none;
  padding: 16px;
  text-align: center;
  width: auto;
  border-radius: 8px;
  background: #d7182a;
  border: 2px solid #d7182a;
  color: #ffffff;
  font-size: 15px;
  font: 700 15px "NeulandGrotesk", Arial, Helvetica, Sans-serif !important;
  line-height: 1.6;
  margin: 10px;
  letter-spacing: 0.65px;
  cursor: pointer;
  transition: 0.05s linear;
  text-transform: uppercase;
  -webkit-tap-highlight-color: transparent;
  max-width: calc(100% - 20px);
  width: 100%;
  display: block;

  * {
    font: 700 15px "NeulandGrotesk", Arial, Helvetica, Sans-serif !important;
  }

  &:hover {
    background: #c41628;
  }

  &:active {
    box-shadow: inset 1px 2px 7px rgba(0, 0, 0, 0.4);
    background: #c41628;
  }

  &:focus {
    background: #c41628;
  }

  &:disabled {
    background: #646c79;
    border-color: #646c79;
    cursor: not-allowed;
  }

  img {
    transform: translate(0, 30%);
    margin: 0 4px;
    display: none;
  }
}

.ButtonReg.outline {
  background: #ffffff;
  color: rgba(16, 27, 38, 1);

  &:active {
    color: rgba(16, 27, 38, 0.8);
    border: 2px solid rgba(215, 24, 42, 0.6);
  }

  &:focus {
    border: 2px solid rgba(215, 24, 42, 0.6);
  }
}

.ButtonReg.inline {
  display: inline-block;
  width: auto;
}

.ButtonReg.withImg {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;

  img {
    display: block;
    max-height: 22px;
    transform: none;
  }
}

.ButtonReg.secondary {
  color: #515965;
  border-color: #cacaca;
  background: #fff;
  box-shadow: none;

  &:hover {
    background: #efefef;
  }

  &:active,
  &:focus {
    border-color: #a9a9a9;
    box-shadow: inset 0 1px 10px rgba(0, 0, 0, 0.4);
  }

  &:disabled {
    background: #dedede;
  }
}

.ButtonReg.textRed {
  color: #d7182a;
}

.ButtonReg.condensed {
  font-family: "NeulandGrotesk-Condensed" !important;
}

.ButtonReg.f18 {
  font-size: 18px;
}

.ButtonReg.f14 {
  font-size: 14px;
}

.ButtonReg.withShadow {
  background-color: #FFFFFF;
  border: unset;
  box-shadow: 0 2px 20px 0 rgba(102,102,102,0.2);

  &:active,
  &:focus {
    box-shadow: 0 2px 20px 0 rgba(217, 0, 0, 0.301);
  }
}
