.form__label {
  font: 600 16px "NeulandGrotesk";
  letter-spacing: 0.48px;
  color: #8895a9;
  font-stretch: condensed;
  display: flex;
  align-items: center;

  .sublabel {
    font-size: 12px;
    margin-left: 6px;
  }

  .red {
    color: #d7182a;
  }
}

textarea.form__control,
input.form__control {
  border: 2px solid #e5e9f2;
  border-radius: 8px !important;
  font: 400 15px "NeulandGrotesk";
  padding: 18px 16px;
  color: #98a7bd;

  &::placeholder {
    font: 400 15px "NeulandGrotesk";
  }

  &:focus {
    color: #515965;
    border: 2px solid #98a7bd;
  }
}

select {
  background: #ffffff;
  border: 2px solid #e5e9f2 !important;
  border-radius: 8px !important;
  padding: 18px !important;
  font: 400 15px "NeulandGrotesk" !important;
  color: #515965 !important;

  option {
    font: 400 15px "NeulandGrotesk" !important;
  }

  &:hover {
    border-color: #515965 !important;
  }

  &:focus {
    border-color: #515965 !important;
    background: rgba(229, 233, 242, 0.4) !important;
  }
}

.selectDropdown {
  position: relative;

  &::after {
    content: "";
    position: absolute;
    right: 0;
    top: 50%;
    border: 2px solid #515965;
    border-right: none;
    border-top: none;
    height: 8px;
    width: 8px;
    transform: translate(-115%, -50%) rotate(-45deg);
    z-index: 2;
  }

  select {
    padding-right: 26px !important;
  }
}

.onlyMobile {
  @media (min-width: 920px) {
    display: none;
  }
}

.errorAPI__view {
  position: relative;
  margin-bottom: 40px;

  .err__msgBig {
    text-transform: uppercase;
    margin: 0 auto 0.5em;
    line-height: 1;
  }

  * {
    font-family: "NeulandGrotesk", Arial, sans-serif;
    line-height: 1.35;
    // letter-spacing: 0.15px;
  }
  a {
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
}

.errorAPI__box {
  position: relative;
  margin: 30px 0px;
  display: flex;
  border-radius: 6px;
  background-color: rgba(208,23,40,0.15);
  grid-gap: 20px;
  padding: 23px 20px;
  color: #5D141B;
  align-items: center;
  font-size: 15px;

  .err__icon {
    width: 20px;
  }

  * {
    font-family: "NeulandGrotesk", Arial, sans-serif;
    line-height: 1.35;
    // letter-spacing: 0.15px;
  }
  a {
    text-decoration: underline;

    &:hover {
      text-decoration: none;
    }
  }
}